.card-cotainer{
    display: flex;
    flex-direction: column;
    background-color: #95dada;
    border:1px solid gray;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    backface-visibility: hidden;
    transform:translateZ(0);
    transition: transform 0.25s ease-out;
}
.card-cotainer:hover{
    transform: scale(1.05);
}

.card-cotainer > img{
    max-height:200px;
    width: auto;
}